*, *::before, *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    height: 100%;
    width: 100%;
    overflow-x: hidden;
}

.main-container {
    padding: var(--size-padding-medium);
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: calc(100vw - 2 * var(--size-padding-medium));
    max-width: 100%;
}

.textarea {
    width: 100%;
    padding: var(--size-padding-medium);
    border: 1px solid var(--color-border);
    border-radius: var(--size-border-radius);
    margin: var(--size-margin-small) 0;
    background-color: var(--color-background-light);
    resize: vertical;
    min-height: 30px;
    overflow: auto;
}

.button {
    height: var(--size-element-height);
    padding: 0 var(--size-padding-medium);
    border: none;
    border-radius: var(--size-border-radius);
    cursor: pointer;
    transition: background-color var(--transition-speed);
    line-height: var(--size-element-height);
    white-space: nowrap;
    font-size: var(--size-font-default);
}

#compression-buttons {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    margin: var(--size-margin-small);
}

#compression-buttons .label {
    margin-right: var(--size-margin-small);
}

.compression-button {
    height: var(--size-element-height);
    margin-right: var(--size-margin-small);
    padding: 0 var(--size-padding-medium);
    border-radius: var(--size-border-radius);
    background-color: var(--color-background-medium);
    color: var(--color-text);
    border: none;
    cursor: pointer;
    transition: background-color var(--transition-speed);
    line-height: var(--size-element-height);
    font-size: var(--size-font-default);
}

.compression-button:hover {
    background-color: var(--color-background-dark);
}

.compression-button.selected {
    background-color: var(--color-background-active);
    color: var(--color-text-light);
}

.webpage-button {
    background-color: var(--color-button);
    color: var(--color-text-light);
}

.webpage-button:hover {
    background-color: var(--color-button-hover);
}

.horizontal-section {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    margin-top: var(--size-margin-medium);
    margin-bottom: var(--size-margin-medium);
}

.counter {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0 var(--size-margin-small);
    flex-basis: auto;
}

.counter .label {
    margin-right: var(--size-margin-small);
}

.input {
    width: var(--width-input);
    height: var(--size-element-height);
    padding: 0 var(--size-padding-small);
    border: none;
    border-radius: var(--size-border-radius);
    background-color: var(--color-background-medium);
    text-align: center;
    cursor: not-allowed;
    color: var(--color-text);
    transition: background-color var(--transition-speed);
    margin: 0 var(--size-margin-small);
    line-height: var(--size-element-height);
    font-size: var(--size-font-default);
}

.input:hover {
    background-color: var(--color-background-dark);
}

.json-types-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: var(--size-margin-medium);
}

.instructions {
    margin-bottom: var(--size-margin-medium);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    text-align: center;
}

.instructions p {
    font-size: var(--size-font-large);
    color: var(--color-text-muted);
    margin: 0;
}

.row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: var(--size-margin-medium) 0;
    width: 100%;
    min-height: calc(var(--size-element-height) + 2 * var(--size-margin-small));
}

.box {
    position: relative;
    display: flex;
    align-items: center;
    height: 30px;
    margin: 2px;
    cursor: grab;
    user-select: none;
    border-radius: var(--border-radius);
    transition: background-color var(--transition-speed), transform 0.5s ease, opacity var(--transition-speed);
    overflow: visible;
}

.box:hover {
    z-index: 1;
}

.box:active {
    cursor: grabbing;
}

.box.hidden {
    opacity: 0.5;
}

.box .icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 100%;
    background-color: var(--icon-bg-color);
    cursor: grab;
    flex-shrink: 0;
}

#row2 .box .icon {
    background-color: var(--icon-bg-color-row2);
}

#row2 .box .right-icon {
    background-color: var(--right-icon-bg-color);
}

.box .left-icon {
    border-top-left-radius: var(--border-radius);
    border-bottom-left-radius: var(--border-radius);
}

.box .left-icon.icon-file::before {
    content: '';
    display: inline-block;
    background: var(--icon-file) no-repeat center center / contain;
    width: 18px;
    height: 18px;
}

.box .left-icon.icon-folder::before {
    content: '';
    display: inline-block;
    background: var(--icon-folder) no-repeat center center / contain;
    width: 18px;
    height: 18px;
}

.box .text {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--text-bg-color);
    cursor: grab;
    height: 100%;
    padding-left: var(--size-padding-small);
    padding-right: var(--size-padding-small);
    color: var(--color-text-light);
    font-size: 0.9em;
}

.box .right-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 100%;
    background-color: var(--right-icon-bg-color);
    border-top-right-radius: var(--border-radius);
    border-bottom-right-radius: var(--border-radius);
    flex-shrink: 0;
}

#row1 .box .text {
    border-top-right-radius: var(--border-radius);
    border-bottom-right-radius: var(--border-radius);
}

#row2 .box .text {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.box .right-icon::before {
    content: '';
    display: inline-block;
    background: var(--icon-eye-open) no-repeat center center / contain;
    width: 18px;
    height: 18px;
}

.box .right-icon.open-eye::before {
    background-image: var(--icon-eye-open);
}

.box .right-icon.closed-eye::before {
    background-image: var(--icon-eye-closed);
}

.remove-button {
    position: absolute;
    top: -10px;
    right: 5px;
    width: 16px;
    height: 16px;
    font-size: 14px;
    line-height: 14px;
    background-color: #ff4d4d;
    color: white;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    display: none;
    justify-content: center;
    align-items: center;
    padding: 0;
    font-weight: bold;
}

.box:hover .remove-button {
    display: flex;
}

.remove-button::after {
    content: '-';
}

.line {
    width: 100%;
    height: 2px;
    background-color: var(--color-border);
    margin: var(--size-margin-medium) 0;
}

.placeholder {
    height: var(--size-element-height);
    background-color: transparent;
    border: 2px dotted var(--color-border);
    margin: var(--size-margin-small);
    border-radius: var(--size-border-radius);
    padding: 0 var(--size-padding-medium);
}

.icon {
    display: inline-flex;
    align-items: center;
    margin-right: var(--size-margin-small);
    width: 1.2em;
    height: 1.2em;
}

.size-6 {
    width: 1.2em;
    height: 1.2em;
}

.filetype-input {
    width: var(--width-filetype-input);
    height: var(--size-element-height);
    padding: 0 var(--size-padding-small);
    margin: var(--size-margin-small) 0;
    border: 1px solid var(--color-border);
    border-radius: var(--size-border-radius);
    font-size: var(--size-font-default);
    display: block;
    margin-left: auto;
    margin-right: auto;
    cursor: text;
    line-height: var(--size-element-height);
}

/* Remove any margin or padding that might be causing gaps */
.box > * {
    margin: 0;
    padding: 0;
}

@media (max-width: 890px) {
    .horizontal-section {
        flex-direction: column;
        align-items: stretch;
    }

    .horizontal-section > * {
        margin-bottom: var(--size-margin-small);
    }

    .json-types-container, .json-types-list {
        padding: 0 var(--size-padding-medium);
    }

    .main-container {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    .button, .input, .compression-button {
        width: 100%;
    }

    .filetype-input {
        width: calc(100% - 2 * var(--size-padding-medium));
    }
}

@media (max-width: 500px) {
    .button, .input, .compression-button, .textarea {
        padding: 0 var(--size-padding-medium);
    }

    .filetype-input {
        font-size: var(--size-font-default);
    }
